.minimized-listing {
    display: flex; /* Use flexbox for layout */
    align-items: center; /* Center the items vertically */
    margin: 2%; /* Add some margin */
    padding: 2%; /* Add some padding */
    border: 1px solid #995b5b; /* Add a border for better visibility */
    border-radius: 5px; /* Add rounded corners */
    background-color: #941c1c; /* Light background color */
}

.mini_img {
    width: auto;
    background: #ddd;

    margin-right: 3%;
}

.product-image {
    width: 30%; 
    max-width: 60%; 
    height: auto;
}

.info_container {
    flex: 2; /* Make the info container take up two parts of the flex space */
}

.product_title {
    margin: 0; /* Remove default margin */
    font-size: 1.5em; /* Set a larger font size for the title */
}

.seller_name {
    margin: 2% 0 0; /* Add some margin above the seller name */
    color: #555; /* Set a different color for the seller name */
}
